
import { Options, Vue } from 'vue-class-component';
import { app_name } from '@/consts';
import { useI18n } from 'vue-i18n';

@Options({
  name: 'HelloWorld',
  props: {
    msg: String,
  },
  computed: {
    app_name: () => app_name,
    t: () => useI18n().t,
  },
})
export default class HelloWorld extends Vue {
  app_name!: string
  msg!: string
}
